import { useSelector } from "react-redux";
import { useState, useEffect } from "react";

import styled from "styled-components";

import {
  State,
  GameState,
  TypeEffect,
  PlayerListType,
} from "../business/types";

import { PlayerStatus } from "./PlayerStatus";
import { PlayersStatusList } from "./PlayersStatusList";

const Status = styled.div`
  width: 200px;
  min-height: 18px;
  width: 250px;
  height: 100px;
  border: 1px solid lightgray;
  background-color: #fff2d9;
  text-align: center;
  font-size: 20px;
  color: #5f5757;
  padding-top: 15px;
  box-sizing: border-box;
`;

export const StatusList = () => {
  type statusType = string;
  const initialStatus: statusType = "";
  const [status, updateStatus] = useState(initialStatus);

  const { dice, playerList, gameState, doEffect, gameResult } = useSelector(
    (state: State) => ({
      ...state,
    })
  );

  const newStatus = getTextStatus(gameState, doEffect, dice, gameResult);
  const batlePhrase = "p–µ–∂–∏–º –±–æ—è";
  useEffect(() => {
    updateStatus(() => {
      if (gameState.type.includes("interactWithEnemy")) {
        if (gameState.type === "interactWithEnemy") {
          return newStatus;
        } else {
          return `${batlePhrase}: ${newStatus}`;
        }
      } else {
        return newStatus;
      }
    });
  }, [gameState.type, doEffect?.type]);
  return (
    <>
      <Status>{status}</Status>
      <PlayerStatus />
      {/*    <PlayersStatusList /> */}
    </>
  );
};

const getTextStatus = (
  gameState: GameState,
  doEffect: TypeEffect,
  dice: number,
  gameResult: string
) => {
  switch (gameState.type) {
    case "gameStarted.trownDice":
      return "–±—Ä–æ—Å–∏—Ç—å –∫—É–±–∏–∫";
    case "gameStarted.playerMove":
      return "—Å–¥–µ–ª–∞—Ç—å —Ö–æ–¥";
    case "gameStarted.takeCard":
      return "–æ—Ç–∫—Ä—ã–≤–∞–µ–º –∫–∞—Ä—Ç–æ—á–∫—É";
    case "gameStarted.applyCard":
      return "–ø—Ä–∏–º–µ–Ω–∏—Ç—å –∫–∞—Ä—Ç–æ—á–∫—É";

    /*   case "interactWithEnemy.applyCard":
      return "–ø—Ä–∏–º–µ–Ω–∏—Ç—å –æ—Ä—É–∂–∏–µ"; */
    case "interactWithEnemy.throwBattleDice": {
      return "–±—Ä–æ—Å–∏—Ç—å –∫—É–±–∏–∫";
    }
    case "interactWithEnemy.applyCard": {
      return "–ø—Ä–∏–º–µ–Ω–∏—Ç—å –æ—Ä—É–∂–∏–µ";
    }

    case "interactWithEnemy.makeBattleAction":
    case "interactWithEnemy":
      switch (doEffect?.type) {
        case "!openEnemyCard": {
          return "–æ—Ç–∫—Ä—ã–≤–∞–µ–º –∫–∞—Ä—Ç–æ—á–∫—É";
        }

        case "!getBattleResult": {
          switch (dice) {
            case 1:
            case 2: {
              return `–≤—ã–ø–∞–ª–æ ${dice}: –∏–≥—Ä–æ–∫ –ø—Ä–∏–º–µ–Ω—è–µ—Ç –æ—Ä—É–∂–∏–µ –∏–ª–∏ –±—Ä–æ—Å–∞–µ—Ç –∫—É–±–∏–∫ `;
            }
            case 3: {
              return `–≤—ã–ø–∞–ª–æ ${dice}: –∏–≥—Ä–æ–∫ —Ç–µ—Ä—è–µ—Ç 1 –∑–¥–æ—Ä–æ–≤—å–µ`;
            }
            case 4: {
              return `–≤—ã–ø–∞–ª–æ ${dice}: –∏–≥—Ä–æ–∫ —Å–ø–∞—Å–∞–µ—Ç—Å—è –±–µ–≥—Å—Ç–≤–æ–º `;
            }
            default:
              return "–±—Ä–æ—Å–∏—Ç—å –∫—É–±–∏–∫ –∏–ª–∏ –ø—Ä–∏–º–µ–Ω–∏—Ç—å –æ—Ä—É–∂–∏–µ";
          }
        }
        default:
          return "—Å–¥–µ–ª–∞—Ç—å —Ö–æ–¥";
      }

    /*   case "gameStarted.interactWithEnemy.fightOrKeepBattle":
      return "–ø—Ä–∏–º–µ–Ω–∏—Ç—å  –æ—Ä—É–∂–∏–µ –∏–ª–∏ –±—Ä–æ—Å–∏—Ç—å –∫—É–±–∏–∫"; */

    case "endGame":
      return gameResult;
    default:
      return "";
  }
};
